import React, { Component } from 'react';
import { Card,Popconfirm,message } from 'antd'
import config from '../../axios/config'
import * as api from '../../axios/api'
import './index.less';

export default class CardListView extends Component {
  constructor(props) {
    super(props)
    this.state = {
        list: this.props.list,
        isShareModalVisible: false,
        visible: false
    }
  }

  openPage = (uuid) => {
    let link = config.url.publicPrefix + '/#/publish/mobile/' + uuid
    window.open(link, '_blank')
  }

  render() {
    const { list,currentPage,perPage } = this.props;
    const defaultIcon = 'static/avatar/默认.gif'
    const text = '你确定要删除该数据快报吗?';

    const confirm = (uuid) => {
      this.props.setState(true)
      api.deleteReport(uuid, currentPage, perPage).then((response) => {
        const data = response.data.data
        this.props.setState(false, data.reportList)
        message.info('删除成功');
      })
    };

    const cardCover = (player,score,uuid) => {
      return (
        <div className='card-cover'>
          <div className='card-delete'>
            <Popconfirm placement="topLeft" title={text}
              onConfirm={(e) => { e.stopPropagation(); confirm(uuid) }}
              onCancel={(e) => { e.stopPropagation() }}
              okText="是"
              cancelText="否">
            <div className='delete-icon' onClick={(e) => {
              e.stopPropagation()
              }}></div>
          </Popconfirm>
          </div>
          <div className="card-player-score">
            <div className="card-player-info">
              <div className="card-player-info-name">{(player[0] && player[0].name) ? player[0].name : '未知'}</div>
              <div className='card-player-info-icon'>
                <img id='icon' className="icon-img" src={(player[0] && player[0].avatar) ? player[0].avatar : defaultIcon} alt='选手头像'
                onError={(e) => { e.target.onerror = null;e.target.src=defaultIcon}}/>
              </div>
            </div>
            <div className="match-score">
              <div className="match-score-vs">vs</div>
              <div className="match-score-detail">
                <div className={score[0] > score[1] ? "score-highlight" : null}>{score[0]}</div>
                <div className='score-divider'>-</div>
                <div className={score[1] > score[0] ? "score-highlight" : null}>{score[1]}</div>
              </div>
            </div>
            <div className="card-player-info" >
              <div className="card-player-info-name">{(player[1] && player[1].name) ? player[1].name : '未知'}</div>
              <div className='card-player-info-icon'>
                <img id='icon' className="icon-img" src={(player[1] && player[1].avatar) ? player[1].avatar : defaultIcon} alt='选手头像'
                  onError={(e) => { e.target.onerror = null;e.target.src=defaultIcon}} />
              </div>
            </div>
          </div>
        </div>
      )
    }

    return (
      <div className='card-wrapper' >
        {
            list && list.length === 0 ? null:
            list.map((item, index) => {
                return (
                  <Card
                    hoverable
                    key={'page'+index}
                    cover={cardCover(item.player, item.score, item.uuid)}
                    onClick={()=>{this.openPage(item.uuid)}}
                    className="card"
                  >
                    <div className='card-content'>
                      <span>{ item.match.matchType }</span>
                      <span>{item.match.matchCompetition}{ item.match.matchRound }</span>
                      <span>{item.player[0].country}vs{item.player[1].country}</span>
                    </div>
                  </Card>)
              })
        } 
      </div >
    )
  }
}